import {Injectable} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {Observable} from "rxjs/Observable";
import {WebPage} from "../models/webpage.model";

@Injectable()
export class WebPageRepository {

  constructor(private restDatesource: RestDatasourceService) {

  }

  getByName(name?: string): Observable<WebPage> {
    return this.restDatesource.get(`webpage/${name}`);
  }

}



