import {Component, Input, OnInit} from '@angular/core';
import {ProjectRepository} from "../../../services/project-repository.service";
import {ProjectSummary} from "../../../models/project-summary.model";
import {Project} from "../../../models/project.model";

@Component({
  selector: 'app-project-sidebar',
  templateUrl: './project-sidebar.component.html',
  styleUrls: ['./project-sidebar.component.css']
})
export class ProjectSidebarComponent implements OnInit {
  projectSummary: ProjectSummary;
  currentProject: Project;
  selectedSection: string;
  hasSection: boolean;

  constructor(private projectRepository: ProjectRepository) {

  }

  @Input()
  set project(project: Project) {
    this.currentProject = project;
    this.projectRepository.getProjectSummary(this.currentProject.id).subscribe(data => {
      this.projectSummary = data;
      this.hasSection = this.projectSummary.sectionNames.length > 0;
    });
  }


  ngOnInit(): void {
  }

  getDocumentTitleByName(sectionName: string): string[] {
    let titles: string[] = [];
    for (let _sectionName of Object.keys(this.projectSummary.documentTitles)) {
      if (_sectionName == sectionName) {
        titles = this.projectSummary.documentTitles[_sectionName];
      }
    }
    return titles;
  }
}



